//Fuentes
$fuente-titulos: 'Ubuntu', sans-serif;
$fuente-textos: 'Montserrat', sans-serif;
$fuente-cursiva: 'Dancing Script', cursive;
$fuente-special: 'Patrick Hand SC', cursive;

//Media Queries
$phone: 480px;
$tablet: 768px;
$desktop: 1080px;

//Colores
$primario: #238C4D;
$secundario: #0ed2f7;
$terciario: #ffbf00;
$negro: #000000;
$blanco: #edf6f9;
$grisClaro: #eceeef;
$luzNoche: 0px 0px 20px 10px rgba(255, 255, 255, 0.8), 0px 0px 40px 20px rgba(255, 255, 255, 0.6), 0px 0px 60px 30px rgba(255, 255, 255, 0.4);

//Fondos
$dia: repeating-radial-gradient( circle at 0 0, transparent 0, #c8b6a3 40px ), repeating-linear-gradient( #c0aa6f34, #b8a56f9c );
$noche: repeating-radial-gradient( circle at 0 0, transparent 0, #00171f 40px ), repeating-linear-gradient( #041b26, #07202e );

$contrasteDia: linear-gradient(to top, #2193b0, #6dd5ed);
$contrasteDia2: linear-gradient(to top, #b2fefa, #0ed2f7);

//Unidades
$separacion: 5rem;

//Transiciones
$tran-btn: all 0.2s ease;
$tran-text: all 0.4s ease;
$tran-bar: all 0.5s ease;